.product {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.5);
}

.product\_inner {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    border-radius: 30px;
    background-color: white;
}

.product img {
    max-width: 400px; 
    height: auto; 
    border: solid #fff 20px; 
    border-radius: 50%;
}

.product button {
    display: inline-block;
    padding: 0.3em 1.2em;
    margin: 0 0.1em 0.1em 0;
    border: 0.16em solid rgba(255,255,255,0);
    border-radius: 2em;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Roboto',sans-serif;
    font-weight: 400;
    color: #000;
    background-color: #fff;
    text-shadow: 0 0.04em 0.04em rgba(0,0,0,0.35);
    text-align: center;
    transition: all 0.2s;
}

.product button:hover {
    border-color: rgba(255,255,255,1);
    background-color: #FFE064;
}

@media only screen and (max-width: 490px) {

    .product\_inner {
        padding: 0;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .product img {
        max-width: 100%;
        border-width: 1px;
    }
}